.logoutBtn {
  min-width: 75px;
  min-height: 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 250ms ease-in-out;
  background-color: rgb(231, 231, 231);
}

.logoutBtn:hover {
  background-color: #fff;
  transform: scale(1.1);
}

.userMenu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 300px;
  color: #b1d7d2;
  
}
.userMenu__userInfo{
  display: flex;
  
}

.userMenu svg{
  fill: #b1d7d2;
  margin-right: 10px;
}
